.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo-img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.nav__list {
  align-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-align: center;
  color: #060b35;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav .is-active {
  color: #00acdc;
  position: relative;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.page {
  scroll-behavior: smooth;
  margin: 0;
  font-family: Roboto, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p, h3 {
  margin: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
}

.card__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card__labe {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 4px 0 0;
}

.stars__star:nth-child(-n+4) {
  background: url("star-active.edda4c43.svg") center no-repeat;
}

.main {
  box-sizing: border-box;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (width >= 1200px) {
  .main {
    padding-inline: 128px;
  }
}
